import React from 'react';
import {getTranslation} from '../../text/translate';
import './Hackathon.css';

const ListDetails = (props) => {
    return (
        <div className={'details'}>
            <h3 className={'detailsTitle'}>{props.details.title}</h3>
            <ul className={'detailsList'}>
                {props.details.items.map((item, index) =>
                    <ListItem key={index} value={item}/>
                )}
            </ul>
        </div>
    )
}

const ListItem = (props) => {
    return (
        <li>{props.value}</li>
    )
}

const Hackathon = (props) => {
    return (
        <div id={'hackathon'} className={'page'}>
            <div className={'container'}>
                <h4 className={'date'}>{getTranslation(props.language, 'hackathon', 'date')}</h4>
                <h2 className={'title'}>{getTranslation(props.language, 'hackathon', 'title')}</h2>
                <div className={'info'}>
                    <ListDetails details={getTranslation(props.language, 'hackathon', 'detailsBefore')}/>
                    <ListDetails details={getTranslation(props.language, 'hackathon', 'detailsDuring')}/>
                    <ListDetails details={getTranslation(props.language, 'hackathon', 'detailsAfter')}/>
                </div>
                <ListDetails details={getTranslation(props.language, 'hackathon', 'detailsGeneral')}/>

                <div className={'button'}>
                    <a className={'button-link'}
                       target={'_blank'}
                       href={'https://docs.google.com/forms/d/e/1FAIpQLSc_3kD0M-azjiVigBEJJtNlIPYhV5R-tmJ6j6pyM5FONrrxlQ/viewform?usp=sf_link'}>
                        {getTranslation(props.language, 'hackathon', 'button')}
                    </a>
                </div>
            </div>
            <img alt={''}/>
        </div>
    )
}

export default Hackathon;
