import React from 'react';
import {getTranslation} from '../../text/translate';
import './Workshops.css';

const ListItem = (props) => {
    return (
        <li>{props.value}</li>
    )
}

const Workshops = (props) => {
    return (
        <div id={'workshops'} className={'page'}>
            <h4 className={'date'}>{getTranslation(props.language, 'workshops', 'date')}</h4>
            <h2 className={'title'}>{getTranslation(props.language, 'workshops', 'title')}</h2>
            <ul className={'details'}>
                {getTranslation(props.language, 'workshops', 'details').map((item, index) =>
                    <ListItem key={index} value={item}/>
                )}
            </ul>
        </div>
    )
}

export default Workshops;
